<template>
  <div>
    <!--  -->
    <el-row class="set-tab-wrapper">
      <el-row class="tab-title">
        
      </el-row>
      <el-row class="tab-radio">
        <el-radio-group v-model="isEnabled" @change="handleChange">
          <el-radio :label="1"></el-radio>
          <el-radio :label="0"></el-radio>
        </el-radio-group>
      </el-row>
    </el-row>
    <template v-if="isEnabled == 1 ? true : false">
      <el-row>
        <el-tag class="teacher-tag" v-for="tag in teacherList" :key="tag.user_id" closable @close="removeTeacher(tag)">
          {{tag.name}}ID{{tag.user_id}}
        </el-tag>
      </el-row>
      <el-row>
        <el-col :span="20">
          <el-select style="width:90%;" v-model="selectValue" filterable remote reserve-keyword
                     placeholder="" clearable :remote-method="remoteMethod" :loading="loading">
            <el-option v-for="item in options" :key="item.user_id" :label="item.name+'-ID:'+item.user_id"
                       :value="item.user_id">
            </el-option>
          </el-select>
        </el-col>
        <el-col :span="4">
          <el-button :disabled="selectValue==undefined||selectValue==null||selectValue==''" type="primary"
                     @click="addTeacher">
          </el-button>
        </el-col>
      </el-row>
    </template>
  </div>
</template>
<style lang="less">
  .teacher-tag {
    margin: 0 10px 10px 0;
  }
</style>

<script>
  // change		
  // visible-change	/	 true false
  // remove-tag	tag	tag
  export default {
    components: {},
    props: [],
    data() {
      return {
        options: [],
        selectValue: [],
        loading: false,
        seachTimeOut: null,
        isHandle: false,
        teacherList: []
      };
    },
    methods: {
      handleVisibleChange(bool) {
        this.isHandle = bool;
      },
      //
      async addTeacher() {
        if(this.teacherList != null){ // 
          if(this.teacherList.length == 4){//
                this.$message({
                message: '',
                type: 'warning'
            });
            this.selectValue = '';
            return false;
          }
          if(this.teacherList.length != 0 ){//
            for(let i=0;i<this.teacherList.length;i++){
              if(this.teacherList[i].user_id == this.selectValue) {
                  this.$message({
                      message: '',
                      type: 'warning'
                  });
                  this.selectValue = '';
                  return false;
              }
            }
          }
        }

        let params = {
          course_id: this.course_id,
          teacher_id: this.selectValue
        };
        let ret = await this.$http.addTeacher(params);
        if (ret.status === 0) {
          this.$message({
            message: "",
            type: "success"
          });
          this.selectValue = '';
          this.getTeacherList();
        } else {
          this.$message({
            message: "",
            type: "error"
          });
        }
      },
      // 
      async removeTeacher(tag) {
        let id = tag.user_id;
        let params = {
          course_id: this.course_id,
          teacher_id: id
        };
        let ret = await this.$http.removeTeacher(params);
        if (ret.status === 0) {
          this.$message({
            message: "",
            type: "success"
          });
          this.getTeacherList();
        } else {
          this.$message({
            message: "",
            type: "error"
          });
        }
      },
      async handleRemoveTag(val) {
      },
      //  
      remoteMethod(query) {
        if (query !== '') {
          clearTimeout(this.seachTimeOut);
          this.seachTimeOut = setTimeout(() => {
            this.loading = true;
            let params = {
              name: query
            };
            this.$http
              .searchTeacher(params)
              .then(res => {
                this.loading = false;
                if (res.status === 0) {
                  this.options = res.result;
                }
              })
              .catch(err => {
                this.loading = false;
              });
          }, 500);
        } else {
          this.options = [];
        }
      },
      // 
      handleChange(value) {
        this.SetCourseDisable();
      },
      //
      async SetCourseDisable() {
        let confirmMsg = this.isEnabled === 0? ' ':''
        let msg = this.isEnabled === 1? '' : '';
        let cource_id = this.$route.params.cid;
        let params = {
          setting_value: this.isEnabled, //0:1:
          setting_key: "course_teacher_open" //prefix:mock:classroom:review:;knowledge_recommend:knowledge_syllabus:;gaodun_course_id:id;classroom_pk_open:pkhandout_download_openstudy_record_opencourse_syllabus_openglive_openglivecourse_teacher_open:
        };
        this.$confirm(confirmMsg, '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            this.$http.SetCourseDisable(cource_id, params).then(ret=>{
              if(ret.status === 0){
              this.$message({
                    message:msg,
                    type:'success'
                  })
                  this.getTeacherList();
                }else{
                  this.$message({
                    message:'',
                    type:'warning'
                  })
                }
            })
        }).catch(() => {
          this.isEnabled = this.isEnabled === 0? 1:0;
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      // 
      async getTeacherList() {
        let ret = await this.$http.getTeacherList(this.course_id);
        if (ret.status === 0) {
          this.teacherList = ret.result;
          //   let list = ret.result;
          //   list.forEach(element => {
          //     this.teacherList.push(element.user_id);
          //   });
          //   this.teacherForm.teacher = ret.result;
        }
      }
    },
    computed: {
      course_id() {
        return this.$route.params.cid;
      },
      isEnabled: {
        get() {
          return this.$store.state.course.course_disable.course_teacher_open;
        },
        set(v) {
          let data = {};
          data.course_teacher_open = v;
          this.$store.dispatch("courseDisable", data);
        }
      }
    },
    mounted() {
    },
    created() {
      this.getTeacherList();
    }
  };
</script>



