<template>
  <div class="exam-time">
    <el-row class="set-tab-wrapper">
      <el-row class="tab-title">
        
      </el-row>
      <el-row class="tab-radio">
        <el-radio-group v-model="isEnabled" @change="handleChange">
          <el-radio :label="1"></el-radio>
          <el-radio :label="0"></el-radio>
        </el-radio-group>
      </el-row>
    </el-row>
    <template v-if="isEnabled===1?true:false">
      <el-table :data="seasonList" border style="width: 90%;margin-top: 16px;">
        <el-table-column label="" align="center" width="60">
          <template slot-scope="scope">
            <span>{{scope.$index + 1}}</span>
          </template>
        </el-table-column>
        <el-table-column label="ID" width="80" align="center">
          <template slot-scope="scope">
            <span>{{scope.row.season_id}}</span>
          </template>
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <span>{{scope.row.time}}</span>
          </template>
        </el-table-column>
        <el-table-column label="" align="center" width="160">
          <template slot-scope="scope">
            <!-- <el-button size="small" type="text" @click="handleDelete(scope.$index, scope.row)">
            </el-button> -->
            <el-button size="small" type="text" @click="handleEdit(scope.$index, scope.row)" style="margin: 0 10px">
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="addBox">
        <div @click="addTableData" style="cursor: pointer;display: inline-block;padding: 0 30px;">
          <i class="additem"></i>
          <el-button type="text" class="addBtn"></el-button>
        </div>
      </div>
    </template>
    <el-dialog :title="isAddSeason?'':''" width="60%" center class="tabplane" top="8%" :visible.sync="dialogVisible"  @close='handleCloseSeasonDialog'>
      <el-steps :active="active" finish-status="finish" simple style="margin-top: -10px;margin-bottom:30px;">
        <el-step :title="item.text" :key="index" v-for="(item,index) in progressText" description=""></el-step>
      </el-steps>
      <!--  -->
      <el-form :model="firstAddForm" ref="firstAddForm" v-show="active === 0" @submit.native.prevent label-width="120px" class="el-ruleForm el-first-form">
        <el-form-item label="" prop="dateFormat" :rules="[{required: true,type:'number',message:'', trigger: ['change','blur']}]">
          <el-radio-group @change="handleRaidoChange" v-model="firstAddForm.dateFormat">
            <el-radio :label="1">-</el-radio>
            <el-radio :label="2">--</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item v-show="firstAddForm.dateFormat === 1" label="" prop="date" :rules="[{required: true,message:'', trigger: ['change','blur']}]">
          <el-date-picker v-model="firstAddForm.date" type="month" value-format="yyyy-MM" placeholder="-">
          </el-date-picker>
        </el-form-item>
        <el-form-item v-show="firstAddForm.dateFormat === 2" label="" prop="date" :rules="[{required: true,message:'', trigger: ['change','blur']}]">
          <el-date-picker type="date" placeholder="--" value-format="yyyy-MM-dd" v-model="firstAddForm.date"></el-date-picker>
        </el-form-item>
        <el-form-item class="coursebtn">
          <el-button style="margin-top:12px;" @click="firstNextSubmit('firstAddForm')"></el-button>
        </el-form-item>
      </el-form>
      <!--  -->
      <el-row v-show="active === 1">
        <el-row>
          <p class="exam-time-form-title"></p>
          <p class="exam-time-form-sub-title"></p>
        </el-row>
        <el-form label-width="-12px" :model="secondAddForm" ref="secondAddForm" class="el-ruleForm">
          <el-row style="margin-top:20px;margin-bottom:10px;">
            <p class="exam-time-form-title"></p>
          </el-row>
          <el-form-item label="" prop="date1" :rules="[{ type: 'array', required: true, message: '', trigger: 'change' }]">
            <el-date-picker v-model="secondAddForm.date1" type="daterange" range-separator="" start-placeholder="" value-format="yyyy-MM-dd" end-placeholder="">
            </el-date-picker>
          </el-form-item>
          <el-row style="margin-top:20px;margin-bottom:10px;">
            <p class="exam-time-form-title"></p>
          </el-row>
          <el-form-item label="" prop="date2" :rules="[{ type: 'array', required: true, message: '', trigger: 'change' }]">
            <el-date-picker v-model="secondAddForm.date2" type="daterange" range-separator="" start-placeholder="" value-format="yyyy-MM-dd" end-placeholder="">
            </el-date-picker>
          </el-form-item>
          <el-form-item class="coursebtn">
            <el-button style="margin-top:12px;" @click="prev"></el-button>
            <el-button style="margin-top:12px;" @click="secondSubmit('secondAddForm')"></el-button>
          </el-form-item>
        </el-form>
      </el-row>
      <!--  -->
      <div class="rulemodule" v-show="active === 2">
        <div class="plan-box">
          <div class="setline"></div>
          <!-- <div class="planwel">
            <span class="icon-plan-rang prompt"></span>
            <div class="plantab promptbox"></div>
          </div> -->
          <div class="scroll-content">
            <div class="planwel" v-for="(val,index) in planlist" :key="index">
              <span class="icon-plan-rang"></span>
              <div class="plantab">
                <div class="planmil">
                  <div class="plan-date black_14">{{val.start_time}}  {{val.end_time}}</div>
                  <div class="plan-content">
                    
                    <template v-for="(ol,index) in val.gradation" class="planref">
                      {{ol.name}}
                      <template v-if="(index+1) != val.gradation.length"></template>
                    </template>
                  </div>
                  <div class="plan-target" v-if="val.target != ''">
                    {{val.target}}
                  </div>
                </div>
                <div class="plan-btns">
                  <span class="deleitd" @click="delplan(val,index)"></span>
                  <span class="deleitd" @click="eidtplan(val,index)"></span>
                </div>
              </div>
            </div>
          </div>
          <div class="planwel outlinehit">
            <span class="icon-plan-rang"></span>
            <div class="addoutline" @click="handleOpenPlanDialog">
              <span class="addbtn"></span>
            </div>
          </div>
        </div>
        <div class="coursebtn" style="text-align:center;">
          <el-button style="margin-top:12px;" @click="prev"></el-button>
          <el-button type="primary" :loading="btnLoading" @click="handleCreateSeason()"> </el-button>
        </div>
        <el-dialog width="60%" center class="innerDialog" top="8%" :title="isAddPlan?'':''" :visible.sync="innerVisible" append-to-body :before-close="toClose">
          <el-form :model="planForm" ref="planForm" @submit.native.prevent label-width="140px" class="el-ruleForm">
            <el-form-item label="" prop="content" :rules="[{ required: true, message: '', trigger: 'change' }]">
              <el-select v-model="planForm.content" multiple style="width:90%;" placeholder="">
                <el-option v-for="val in daration_list" :key="val.id" :label="val.name" :value="val.id"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" prop="date" :rules="[{ type: 'array', required: true, message: '', trigger: 'change' }]">
              <el-date-picker v-model="planForm.date" type="daterange" range-separator="" start-placeholder="" value-format="yyyy-MM-dd" end-placeholder="">
              </el-date-picker>
            </el-form-item>
            <el-form-item label="()" prop="target" :rules="filter_rules({type:'isAllSpace',maxLength:100})">
              <el-input type="textarea" style="width:90%;" v-model="planForm.target"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="resetForm('planForm')"></el-button>
            <el-button type="primary" :loading="palnBtnLoading" @click="handleSavePlan('planForm')"> </el-button>
          </div>
        </el-dialog>
      </div>
    </el-dialog>
  </div>
</template>
<style lang="less">
