<script>
import { examTimeProgressText } from "../../common/courseConfig.js";

export default {
  data() {
    return {
      newPlanlist:[],//
      planlist: [],
      dialogVisible: false,
      innerVisible: false,
      tableData: [],
      file: [],
      currentIndex: "",
      seasonList: [], //
      active: 0,
      progressText: examTimeProgressText,
      firstAddForm: {
        dateFormat: 1, //
        date: "" //
      },
      secondAddForm: {
        date1: "",
        date2: ""
      },
      planForm: {
        content: [],
        date: "",
        target: ""
      },
      isAddSeason: true, //
      isAddPlan: true, //
      btnLoading: false, //loading
      palnBtnLoading: false, //loading
      currentPlanIndex: "", //
      seasonId: "", //id
      daration_list:[],//
    };
  },
  // watch:{
  //   planlist:function () {
  //     this.planlist.sort(this.compare('start_time'));
  //     console.log(this.planlist,'////////////');
  //     // start_time
  //   }
  // },
  methods: {
    // 
    toClose(){
      if(!this.isAddPlan){
        this.newPlanlist.forEach(o=>{
          this.daration_list.forEach((x,i)=>{
            if(x.id == o.id){
              this.daration_list.splice(i,1);
            }
          })
        })
      }
      this.newPlanlist = [];
      this.innerVisible = false;
      this.$refs.planForm.clearValidate();//
    },

    //
    compare(property) {
      return function(a, b) {
        try {
          var value1 = new Date(a[property]).getTime();
          var value2 = new Date(b[property]).getTime();
        } catch (error) {
          // console.log(error);
        }
        return value1 - value2;
      };
    },
    // 
    handleSavePlan(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          // 
          const { content, target, date } = this.planForm;
          let gradation = [];

          content.forEach(id => {
            this.daration_list.forEach((ele,i) => {
              if (ele.id == id) {
                gradation.push(ele);
                this.daration_list.splice(i,1);
              }
            });
          });
          // 
          if (this.isAddPlan) {
            this.planlist.push({
              gradation: gradation,
              target,
              start_time: date[0],
              end_time: date[1]
            });
          } else {
            this.planlist[this.currentPlanIndex] = {
              gradation: gradation,
              target,
              start_time: date[0],
              end_time: date[1]
            };
          }
          this.planlist.sort(this.compare("start_time"));
          this.resetForm("planForm");
          this.innerVisible = false;
        } else {
          return false;
        }
      });
    },
    // 
    delplan(v, i) {
      if(v.gradation != null && v.gradation != undefined){
        v.gradation.forEach(o=>{
          this.daration_list.push(o);
        })
      }
      this.planlist.splice(i, 1);
    },
    // 
    handleCloseSeasonDialog(){
      this.getCourseSeasonList();
      // 
    },
    // 
    eidtplan(v, i) {
      // 
      this.newPlanlist = [];
      if(v.gradation != null && v.gradation != undefined){
        v.gradation.forEach(o=>{
          this.daration_list.push(o);
          this.newPlanlist.push(o);
        })
      }
      this.currentPlanIndex = i;
      this.isAddPlan = false;
      const { gradation, plan_id, target, start_time, end_time } = v;
      let date = new Array(2);
      let content = new Array();
      date[0] = start_time;
      date[1] = end_time;
      gradation.forEach((item, index) => {
        content.push(item.id);
      });
      this.planForm = {
        content: content,
        date: date,
        target: target
      };
      this.innerVisible = true;
    },
    // 
    checkSeasonTime(dateForm) {
      let beginTime1 = new Date(dateForm.date1[0]).getTime();
      let endTime1 = new Date(dateForm.date1[1]).getTime();
      let beginTime2 = new Date(dateForm.date2[0]).getTime();
      let endTime2 = new Date(dateForm.date2[1]).getTime();
      if (beginTime1 > endTime1 || beginTime2 > endTime2) {
        return { bool: false, message: "" };
      }
      let status = beginTime2 - beginTime1;
      let status2;
      if (status > 0) {
        status2 = beginTime2 - endTime1;
        if (status2 > 0) {
          return { bool: true };
        } else {
          return { bool: false, message: "" };
        }
      } else {
        return { bool: false, message: "" };
      }
    },
    // 
    handleOpenPlanDialog() {
      this.planForm = {
        content: [],
        date: "",
        target: ""
      };
      this.isAddPlan = true;
      this.innerVisible = true;
    },
    // /
    async handleCreateSeason() {
      let params = {
        course_id: this.course_id, //id
        enable: this.isEnabled, //0:;1:;2:
        time: this.firstAddForm.date, //  -/--
        time_type: this.firstAddForm.dateFormat, //1:-2:--
        "confirm[second][start_time]": this.secondAddForm.date1[0], //
        "confirm[second][end_time]": this.secondAddForm.date1[1], //
        "confirm[third][start_time]": this.secondAddForm.date2[0], //
        "confirm[third][end_time]": this.secondAddForm.date2[1] //
      };
      if (
        this.planlist == null ||
        this.planlist == undefined ||
        this.planlist == "" ||
        this.planlist.length === 0
      ) {
        this.$message({
          message: "",
          type: "warning"
        });
        return;
      }
      this.planlist.forEach((val, index) => {
        let gradation = val.gradation;
        let content = [];
        gradation.forEach(item => {
          //  = item.id;
          content.push(item.id);
        });
        params[`plan[${index}][content][]`] = content;
        params[`plan[${index}][start_time]`] = val.start_time;
        params[`plan[${index}][end_time]`] = val.end_time;
        params[`plan[${index}][target]`] = val.target;
      });
      let ret;
      if (this.isAddSeason) {
        this.btnLoading = true;
        ret = await this.$http.createSeason(params);
      } else {
        let seasonId = this.seasonId;
        this.btnLoading = true;
        ret = await this.$http.updateSeason(seasonId, params);
      }

      this.btnLoading = false;
      if (ret.status === 0) {
        this.$message({
          message: this.isAddSeason ? "" : "",
          type: "success"
        });
        this.dialogVisible = false;
      } else {
        this.$message({
          message: this.isAddSeason
            ? ":" + ret.message
            : ":" + ret.message,
          type: "error"
        });
      }
    },
    handleRaidoChange(r) {
      this.firstAddForm.date = ""; //
    },
    //
    firstNextSubmit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (this.active >= this.progressText.length - 1) return;
          this.active++;
        } else {
          return false;
        }
      });
    },
    //
    secondSubmit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let ret = this.checkSeasonTime(this.secondAddForm);
          if (!ret.bool) {
            return this.$message({
              message: ret.message,
              type: "error"
            });
          }
          if (this.active >= this.progressText.length - 1) return;
          this.active++;
        } else {
          return false;
        }
      });
    },
    prev() {
      if (this.active <= 0) return;
      this.active--;
    },
    handleChange(value) {
      this.SetCourseDisable();
    },
    // 
    SetCourseDisable() {
      let cource_id = this.course_id;
      let params = {
        setting_value: this.isEnabled, //0:1:
        setting_key: "season_manage_open" //prefix:mock:classroom:review:;knowledge_recommend:knowledge_syllabus:;gaodun_course_id:id;classroom_pk_open:pkhandout_download_openstudy_record_opencourse_syllabus_openglive_opengliveseason_manage_open 
      };
      let confirmMsg = this.isEnabled === 0? ' ':''
      this.$confirm( confirmMsg , '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$http.SetCourseDisable(cource_id, params).then(ret=>{
              if (ret.status === 0) {
                this.$message({
                  message: this.isEnabled === 0 ? "" : "",
                  type: "success"
                });
              } else {
                this.$message({
                  message: this.isEnabled === 0 ? "" : "",
                  type: "error"
                });
                this.isEnabled = this.isEnabled === 1 ? 0 : 1;
              }
            })
        }).catch(() => {
          this.isEnabled = this.isEnabled === 0? 1:0;
          this.$message({
            type: 'info',
            message: ''
          });
        });
    },
    // //
    async getCourseSeasonList() {
      let course_id = this.course_id;
      let ret = await this.$http.getSeasonList(course_id);
      if (ret.status === 0) {
        this.seasonList = ret.result.list;
        this.$store.dispatch("getSeasonList", ret.result.list);
      }
    },
    //
    handleDelete(index, row) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.DeleteSeason(row.season_id);
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    // 
    async DeleteSeason(id) {
      let ret = await this.$http.deleteSeason(id);
      if (ret.status === 0) {
        this.$message({
          type: "success",
          message: "!"
        });
      } else {
        ret.message ? ret.message : "";
        this.$message.error(ret.message);
      }
    },
    // //
    async addTableData() {
     let res = await this.$http.getNewSeason(this.$route.params.cid)
      if(res.status === 0){
        this.daration_list = res.result;
      }else {
        this.$message({
          message: '',
          type: 'warning'
        });
        return
      }
      this.firstAddForm = {
        dateFormat: 1, //
        date: "" //
      };
      this.secondAddForm = {
        date1: "",
        date2: ""
      };
      this.planlist = [];
      this.isAddSeason = true;
      this.active = 0;
      this.dialogVisible = true;
    },
    // //
    async handleEdit(index, row) {
      let params = {
        season_id:row.season_id
      }
      let ret = await this.$http.getNewSeason(this.$route.params.cid,params);
      if(ret.status === 0){
        this.daration_list = ret.result;
      } else {
          this.$message({
            message: '',
            type: 'warning'
          });
          return ;
      }

      let item = row;
      this.active = 0;
      this.seasonId = row.season_id; //id
      this.isAddSeason = false;
      this.firstAddForm.dateFormat = parseInt(item.time_type); //
      this.firstAddForm.date = item.time; //
      try {
        this.secondAddForm = {
          date1: [
            item.confirm[0].cycle.start_time,
            item.confirm[0].cycle.end_time
          ],
          date2: [
            item.confirm[1].cycle.start_time,
            item.confirm[1].cycle.end_time
          ]
        };
      } catch (error) {
        this.secondAddForm = {
          date1: "",
          date2: ""
        };
      }
      if(item.plan === null || item.plan === undefined){
        item.plan = []
      }
        this.planlist = item.plan;
      // 
      let arr = [];
      if(this.planlist != null && this.planlist != undefined){
        this.planlist.forEach(o=>{
          if(o.gradation != null && o.gradation != undefined){
            o.gradation.forEach(x=>{
              arr.push(x)
            })
          }
        })
      }
      arr.forEach(o=>{
        if(this.daration_list != null && this.daration_list != undefined){
          this.daration_list.forEach((x,i)=>{
            if(x.id == o.id){
              this.daration_list.splice(i,1);
            }
          })
        }
      })

      this.currentIndex = index;
      this.dialogVisible = true;
    },
    // 
    resetForm(formName) {
      this.planForm.content = [];
      this.planForm.date = [];
      this.planForm.target = "";
      this.$refs[formName].clearValidate();
    }
  },
  computed: {
    course_id() {
      return this.$route.params.cid;
    },
    // daration_list() {
    //   return this.$store.state.course.course_daration_list;
    // },
    isEnabled: {
      get() {
        return this.$store.state.course.course_disable.season_manage_open;
      },
      set(v) {
        let data = {};
        data.season_manage_open = v;
        this.$store.dispatch("courseDisable", data);
      }
    }
  },
  mounted() {
    // Vue.$on('uploadFile', (res, fileList) => {
    // })
  },
  created() {
    // this.GetCourseDisable();
    this.getCourseSeasonList();
  }
};
</script>



