<template>
  <div>
    <!---->
    <el-form ref="firstForm" :model="ruleForm" label-width="240px" label-position="left" :inline="true" style="margin-top:22px;">
      <div class="fromtitle">
        <i class="titicon"></i>
        <span class="tittxt"></span>
      </div>
      <el-form-item label="" class="first-form-item">
        <el-radio-group v-model="ruleForm.bEnabled">
          <el-radio label="0"></el-radio>
          <el-radio label="1"></el-radio>
        </el-radio-group>
      </el-form-item>

      <template v-if="ruleForm.bEnabled=='0'?true:false">
        <el-table :data="tableData" border style="width: 90%;margin-top: 16px;" :show-header="false">
          <el-table-column width="260" align="center">
            <template slot-scope="scope">
              <span>{{scope.row.name}}</span>
            </template>
          </el-table-column>
          <el-table-column width="200" align="center">
            <template slot-scope="scope">
              <span>{{scope.row.outline}}</span>
            </template>
          </el-table-column>
          <el-table-column width="100" align="center">
            <template slot-scope="scope">
              <span>{{scope.row.starttime}}</span>
            </template>
          </el-table-column>
          <el-table-column width="100" align="center">
            <template slot-scope="scope">
              <span>{{scope.row.lasttime}}</span>
            </template>
          </el-table-column>
          <el-table-column width="140" align="center">
            <template slot-scope="scope">
              <span>{{scope.row.people}}</span>
            </template>
          </el-table-column>
          <el-table-column label="" align="center">
            <template slot-scope="scope">
              <el-button size="small" type="text" @click="handleDelete(scope.$index, scope.row)" style="margin: 0 10px"></el-button>
              <el-button size="small" type="text" @click="handleEdit(scope.$index, scope.row)" style="margin: 0 10px"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="addBox">
          <div @click="addTableData" style="cursor: pointer;display: inline-block;padding: 0 30px;">
            <i class="additem"></i>
            <el-button type="text" class="addBtn"></el-button>
          </div>
        </div>
      </template>


    </el-form>

    <el-dialog class="addContent tabplane" title="" :visible.sync="dialogVisible" @close="cancel('NewTableForm')">
      <el-form :model="NewTableForm" :rules="rules" ref="NewTableForm" label-width="100px">
        <el-form-item label="" prop="name">
          <el-input v-model="NewTableForm.name" auto-complete="off" class="coursetxt" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="outline">
          <el-select v-model="NewTableForm.outline" placeholder="">
            <el-option label="21321" value=""></el-option>
            <el-option label="21323" value=""></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="starttime">
          <el-date-picker v-model="NewTableForm.starttime" format="yyyy-MM-dd" type="date" placeholder=""></el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="lasttime">
          <el-date-picker v-model="NewTableForm.lasttime" format="yyyy-MM-dd" type="date" placeholder=""></el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="people">
          <el-input v-model="NewTableForm.people"  auto-complete="off" placeholder="" class="coursetxt"></el-input>
        </el-form-item>
        <el-form-item class="coursebtn">
          <el-button @click="cancel('NewTableForm')"> </el-button>
          <el-button type="primary" @click="updateTable('NewTableForm')" v-if="Doing=='update'"> </el-button>
          <el-button type="primary" @click="addTable('NewTableForm')" v-if="Doing=='addDate'"> </el-button>
        </el-form-item>
      </el-form>

    </el-dialog>


  </div>
</template>

<script>
  import {format} from '../../../util/util'
  export default {
    components: {},
    data() {
      return {
        dialogVisible: false,
        ruleForm: {
          bEnabled: '1',
        },
        NewTableForm: {},
        tableData: [
          {
            name: 'CMA 201812',
            outline: 'CMA 201812',
            starttime: '2018-09-01',
            lasttime: '2018-10-01',
            people:''
          }, {
            name: 'CMA 201812',
            outline: 'CMA 201812',
            starttime: '2018-09-01',
            lasttime: '2018-10-01',
            people:''
          }, {
            name: 'CMA 201812',
            outline: 'CMA 201812',
            starttime: '2018-09-01',
            lasttime: '2018-10-01',
            people:''
          }
        ],
        rules: {
          name: [
            { required: true, message: '', trigger: 'blur' }
          ],
          outline: [
            { required: true, message: '', trigger: 'change' }
          ],
          starttime: [
            { type: 'date', required: true, message: '', trigger: 'change' }
          ],
          lasttime: [
            { type: 'date', required: true, message: '', trigger: 'change' }
          ],
          people: [
            { type: 'string', required: true, message: '', trigger: 'blur' }
          ]
        },
        currentIndex: '',
        Doing: ''
      }
    },
    methods: {
      // 
      addTable(formName){
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.NewTableForm.starttime = format(this.NewTableForm.starttime);
            this.NewTableForm.lasttime = format(this.NewTableForm.lasttime);
            this.tableData.push(this.NewTableForm);
            this.dialogVisible = false;
          } else {
            // console.log('error submit!!');
            return false;
          }
        });
      },
      //
      updateTable(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.tableData[this.currentIndex].name = this.NewTableForm.name;
            this.tableData[this.currentIndex].outline = this.NewTableForm.outline;
            this.tableData[this.currentIndex].starttime = format(this.NewTableForm.starttime);
            this.tableData[this.currentIndex].lasttime = format(this.NewTableForm.lasttime);
            this.tableData[this.currentIndex].people = this.NewTableForm.people;
            this.dialogVisible = false;
          } else {
            // console.log('error submit!!');
            return false;
          }
        });
      },
      handleDelete(index, row) {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.tableData.splice(index, 1)
          this.$message({
            type: 'success',
            message: '!'
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
      },
      //
      addTableData(){
        this.NewTableForm = {
          name: '',
          outline: '',
          starttime: '',
          lasttime: '',
          people:''
        }
        this.Doing = 'addDate';
        this.dialogVisible = true
      },
      //
      handleEdit(index, row) {
        this.operation = 'update';
        this.StageFrom = {...this.tableData[index]};
        this.currentIndex = index;
        this.dialogVisible = true;
      },
      cancel(formName) {
        this.$refs[formName].resetFields();
        this.dialogVisible = false;
      },
    },
    computed: {},
    mounted() {
    },
    created() {
    }
  }
</script>



