<template>
  <div>
    <!---->
    <el-form ref="ruleForm" :model="ruleForm" label-width="240px" label-position="left" :inline="true">
      <div class="fromtitle">
        <i class="titicon"></i>
        <span class="tittxt"></span>
      </div>
      <el-form-item label="" class="first-form-item">
        <el-radio-group v-model="ruleForm.bEnabled">
          <el-radio label="0"></el-radio>
          <el-radio label="1"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="" prop="id" v-show="bShow">
        <el-input v-model="ruleForm.id" placeholder="ID"></el-input>
      </el-form-item>
      <el-form-item label="" prop="name" class="w_350" v-show="bShow">
        <el-input v-model="ruleForm.name" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="outline" class="w_350" v-show="bShow">
        <el-select v-model="ruleForm.outline" clearable placeholder="">
          <el-option label="" value="shanghai"></el-option>
          <el-option label="" value="beijing"></el-option>
        </el-select>
      </el-form-item>
      <el-button type="primary" v-show="bShow"></el-button>

    </el-form>

  </div>
</template>
<style>

</style>
<script>
  export default {
    data() {
      return {
        ruleForm: {
          bEnabled: '1',
          id: '',
          name: '',
          outline: '',
        },
      }
    },
    methods: {},
    computed: {
      bShow(){
        if(this.ruleForm.bEnabled=='0'){
          return true;
        }else{
          return false;
        }
      }
    },
    mounted() {
    },
    created() {
    }
  }
</script>


