<template>
  <div>
    <!---->
    <el-form ref="firstForm" :model="ruleForm" label-width="240px" label-position="left" :inline="true">
          <div class="fromtitle">
            <i class="titicon"></i>
            <span class="tittxt">+</span>
          </div>
          <el-form-item label="" class="first-form-item">
            <el-radio-group v-model="ruleForm.bEnabled">
              <el-radio label="0"></el-radio>
              <el-radio label="1"></el-radio>
            </el-radio-group>
          </el-form-item>

          <template v-if="ruleForm.bEnabled=='0'?true:false">
            <el-table :data="tableData" border style="width: 90%;margin-top: 16px;" :show-header="false">
              <el-table-column width="240" align="center">
                <template slot-scope="scope">
                  <span>{{scope.row.name}}</span>
                </template>
              </el-table-column>
              <el-table-column width="180" align="center">
                <template slot-scope="scope">
                  <span>ID{{scope.row.testid}}</span>
                </template>
              </el-table-column>
              <el-table-column width="180" align="center">
                <template slot-scope="scope">
                  <span>ID{{scope.row.liveid}}</span>
                </template>
              </el-table-column>
              <el-table-column width="180" align="center">
                <template slot-scope="scope">
                  <span>ID{{scope.row.knowledgeid}}</span>
                </template>
              </el-table-column>
              <el-table-column align="center">
                <template slot-scope="scope">
                  <el-button size="small" type="text" @click="handleDelete(scope.$index, scope.row)" style="margin: 0 10px"></el-button>
                  <el-button size="small" type="text" @click="handleEdit(scope.$index, scope.row)" style="margin: 0 10px"></el-button>
                </template>
              </el-table-column>
            </el-table>
            <div class="addBox">
              <div @click="addTableData" style="cursor: pointer;display: inline-block;padding: 0 30px;">
                <i class="additem"></i>
                <el-button type="text" class="addBtn"></el-button>
              </div>
            </div>
          </template>


        </el-form>

    <el-dialog class="addContent tabplane" title="" :visible.sync="dialogVisible" @close="cancel('NewTableForm')">
      <el-form :model="NewTableForm" :rules="rules" ref="NewTableForm" label-width="100px">
        <el-form-item label="" prop="name">
          <el-input v-model="NewTableForm.name"  auto-complete="off" class="coursetxt" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="ID" prop="testid">
          <el-input v-model.number="NewTableForm.testid"  auto-complete="off" class="coursetxt" placeholder="ID"></el-input>
        </el-form-item>
        <el-form-item label="ID()" prop="liveid">
          <el-input v-model.number="NewTableForm.liveid"  auto-complete="off" class="coursetxt" placeholder="ID()"></el-input>
        </el-form-item>
        <el-form-item label="ID" prop="knowledgeid">
          <el-input v-model="NewTableForm.knowledgeid"  auto-complete="off" class="coursetxt" placeholder="ID()"></el-input>
        </el-form-item>
        <el-form-item class="coursebtn">
          <el-button @click="cancel('NewTableForm')"> </el-button>
          <el-button type="primary" @click="updateTable('NewTableForm')" v-if="Doing=='update'"> </el-button>
          <el-button type="primary" @click="addTable('NewTableForm')" v-if="Doing=='addDate'"> </el-button>
        </el-form-item>
      </el-form>
    </el-dialog>


  </div>
</template>

<script>
  export default {
    components: {},
    data() {
      return {
        dialogVisible: false,
        ruleForm: {
          bEnabled: '1',
          id: '',
          name: '',
          outline: '',
        },
        NewTableForm: {
          name: '',
          testid: '',
          liveid: '',
          knowledgeid: '',
        },
        tableData: [
          {
            name: '',
            testid: 1233,
            liveid: 12312,
            knowledgeid: 12312,
          }, {
            name: '',
            testid: 1233,
            liveid: 12312,
            knowledgeid: 12312,
          }, {
            name: '',
            testid: 1233,
            liveid: 12312,
            knowledgeid: 12312,
          }
        ],
        rules: {
          name: [
            { required: true, message: '', trigger: 'blur' }
          ],
          testid: [
            { required: true, type:'number', message: 'ID', trigger: 'blur' }
          ]
        },
        currentIndex: '',
        Doing: 'addDate'
      }
    },
    methods: {
      // 
      addTable(formName){
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.tableData.push(this.NewTableForm);
            this.dialogVisible = false;
          } else {
            // console.log('error submit!!');
            return false;
          }
        });
      },
      //
      updateTable(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            for(var key in this.NewTableForm){
              this.tableData[this.currentIndex][key] = this.NewTableForm[key]
            }
            this.dialogVisible = false
          } else {
            // console.log('error submit!!');
            return false;
          }
        });
      },
      handleDelete(index, row) {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.tableData.splice(index, 1)
          this.$message({
            type: 'success',
            message: '!'
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
      },
      //
      addTableData(){
        this.NewTableForm= {
          name: '',
          testid: '',
          liveid: '',
          knowledgeid: '',
        }
        this.Doing = 'addDate';
        this.dialogVisible = true
      },
      handleEdit(index, row) {
        this.Doing = 'update';
        this.NewTableForm = {...this.tableData[index]};
        this.currentIndex = index;
        this.dialogVisible = true;
      },
      cancel(formName) {
        this.$refs[formName].resetFields();
        this.dialogVisible = false;
      },
    },
    computed: {},
    mounted() {
    },
    created() {
    }
  }
</script>



